import { Component, ViewChild } from '@angular/core';
import { MatTableDataSource, MatPaginator, MatSort } from '@angular/material';


@Component({
    selector: 'backup',
    templateUrl: './backup.component.html',
    styleUrls: ['./backup.component.css'],
  
})
export class BackupComponent {
    titulos = ['data', 'autor', 'status', 'acoes'];
    dados = new MatTableDataSource(this.gerarAmostra(100));

    @ViewChild(MatSort) sort: MatSort;
    @ViewChild(MatPaginator) paginator: MatPaginator;

    ngAfterViewInit() {
        this.dados.sort = this.sort;
        this.dados.paginator = this.paginator;
    }

    gerarAmostra(quantidade: number) {
        var dados = [];
        for (let i = 0; i < quantidade; i++) {
            var acoes: Acoes = { detalhar: true };

            var info: Dados =
                {
                    id: i,
                    data: gerarDia(),
                    autor: gerarAutor(),
                    status: gerarStatus(),
                    acoes,

                }
            dados.push(info);
        }
        return dados;
    }

    abrirDetalhes(id): void {
        console.log(id);
    }
}

// Amostra
interface Dados {
    id: number;
    data: Date;
    autor: string;
    status: Status;
    acoes: Acoes;
}

interface Acoes {
    detalhar: boolean;
}

enum Status {
    Concluido = "Concluido",
    Erro = "Erro"
}

function gerarStatus() {
    return Math.round(Math.random()) == 1 ? Status.Concluido : Status.Erro;
}
function gerarAutor() {
    return Math.random() >= 0.3 ? "Automático (Sistema)" : "João";
}

function gerarDia() {
    var dia: Date = new Date();
    //Num. aleatório entre -50 e 50;
    var diasExtras = (Math.random() * 51) * (Math.round(Math.random()) == 1 ? 1 : -1);
    dia.setDate(dia.getDate() + diasExtras);
    return dia;
}



