import { Routes, RouterModule } from '@angular/router';

import { AppComponent } from './app.component';
import { HomeComponent } from './home.component';
import { ManterUsuarioComponent } from './manter-usuario/manter-usuario.component';
import { NotificacoesComponent } from './notificacoes.component';
import { LinksComponent } from './links.component';
import { BackupComponent } from './backup/backup.component';
import { LoginComponent } from './login.component';

import { LoginGuard } from "./login.guard";

const routes: Routes = [
    {path: 'home', component: HomeComponent },
    {path:'', component: BackupComponent, canActivate:[LoginGuard]},
    {path:'usuarios', component: ManterUsuarioComponent, canActivate:[LoginGuard]},
    {path:'notificacoes', component: NotificacoesComponent},
    {path:'links', component: LinksComponent, canActivate:[LoginGuard]},
    {path:'backup', component: BackupComponent, canActivate:[LoginGuard]},
    {path:'login', component: LoginComponent}
];

export const routing = RouterModule.forRoot(routes);


