import { NgModule } from '@angular/core';

import {
  MatIconModule,
  MatToolbarModule,
  MatButtonModule,
  MatTableModule,
  MatPaginatorModule,
  MatFormFieldModule,
  MatInputModule,
  MatMenuModule,
  MatSelectModule,
  MatCardModule,
  MatSortModule,
  MatPaginatorIntl,
  MatTabsModule,
  MatTooltipModule,
  MatGridListModule,
  MatDividerModule
} from '@angular/material';

import { MatPaginatorIntlPtBr } from './MatPaginatorIntl_pt-br';

@NgModule({
  imports:[],
  exports:[
    MatIconModule,
    MatToolbarModule,
    MatButtonModule,
    MatTableModule,
    MatPaginatorModule,
    MatFormFieldModule,
    MatInputModule,
    MatMenuModule,
    MatSelectModule,
    MatCardModule,
    MatSortModule,
    MatTabsModule,
    MatTooltipModule,
    MatGridListModule,
    MatDividerModule
  ],
  declarations: [],
  providers: [
    { provide: MatPaginatorIntl, useClass: MatPaginatorIntlPtBr }]
})
export class AngularMaterialModule { }



