import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';

import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { PathLocationStrategy, LocationStrategy } from '@angular/common';
import { RouterModule } from "@angular/router";

import { Services } from "../services";
import { routing } from "./app.routing";
import { AppComponent } from './app.component';
import { LinksComponent } from './links.component';
import { HomeComponent } from './home.component';
import { NotificacoesComponent } from './notificacoes.component';
import { BackupComponent } from './backup/backup.component';
import { LoginComponent } from './login.component';
import { MenuComponent } from './menu.component';

import { AngularMaterialModule } from './angular-material/angular-material.module';
import { UsuarioModule } from './manter-usuario/manter-usuario.module';

import { LoginGuard } from './login.guard';
import { ConfigurationModule } from '@stefanini/configuration';

import { HttpClientModule } from '@angular/common/http';

export const APP_CONFIG = {
  'my-config': 'My Config',
  'my-config-env': {
    'dev': 'Config Dev',
    'prod': 'Config Dev'
  }
};

// import url-resolver module
import { UrlResolverModule } from '@stefanini/url-resolver';

export const APP_ROUTE = {
  '_defaults': {
    'host': 'http://api.example.com/'
  },
  'user': {
    'url': 'users/{id}',
    'method': 'GET',
    'headers': {
      'content-type': 'application/json'
    },
    'params': {
      'id': {
        'type': 'number',
        'required': true
      }
    }
  },
  'testepost' : {
    'url': 'http://localhost:8080/testepost',
    'method': 'POST',
    'headers': {
      'content-type': 'application/x-www-form-urlencoded'
    }
  },
  // 'testepost': {
  //  // 'url': 'https://homolog.sgi.sebrae.com.br/OAuth_Authorization/OAuth/Token',

  //   'url': 'http://localhost:8080/testepost',
  //   'method': 'POST',
  // },
  'url-auth': {
    'url': 'https://homolog.sgi.sebrae.com.br/OAuth_Authorization/OAuth/Token',
    'method': 'POST',
  },
  
  'github': {
    'url': 'https://api.github.com/users/seeschweiler',
    'method': 'GET',
  },
  'resolver': {
    'url': 'http://localhost:8080/resolver',
    'method': 'POST',
  }
};

// Import http module
 import { HttpModule, DefaultPlugins } from '@stefanini/http';




@NgModule({
  declarations: [
    AppComponent, LinksComponent, MenuComponent, HomeComponent, NotificacoesComponent, BackupComponent, LoginComponent
  ],
  imports: [
    RouterModule,
    routing,
    BrowserModule,
    CommonModule,
    AngularMaterialModule,
    FormsModule,
    UsuarioModule,
    ConfigurationModule.initialize(APP_CONFIG, 'dev'),
    UrlResolverModule.initialize(APP_ROUTE),
    HttpModule.initialize(DefaultPlugins),
    HttpClientModule

  ],
  exports: [
    AngularMaterialModule,
    BrowserAnimationsModule,

  ],
  providers: [
    Services,
    { provide: LocationStrategy, useClass: PathLocationStrategy },
    LoginGuard,
    // Services
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }


